.about {

  &__title {
    margin: 0  0 48px;
    font-weight: 700;
    font-size: 36px;
    line-height: 36px;
    letter-spacing: -1.5px;
    color: #000;

    @include onDesktop {
      margin-bottom: 0;
      font-size: 48px;
      line-height: 48px;
    }
  }
  &__title--link {
    color: $color-blue;
    text-decoration: none;
  }

  &__subtitle {
    display: block;
    margin-bottom: 16px;

    font-weight: 700;
    font-size: 24px;
    line-height: 30px;
    color: $color-blue;
  }

  &__paragraph {
    margin: 0 0 27px;
    font-weight: 400;
    font-size: 16px;
    line-height: 27px;

    &:last-child {
      margin: 0;
    }

    &--important {
      font-weight: bold;
    }
  }
}
